<template>

  <div class="base">
    <div id="panoramaConianer"></div>
  </div>
  <!-- <img src="../../assets/img/p3.png"
    alt=""> -->

</template>

<script>
import { tpanorama } from 'tpanorama'
export default {
  data () {
    return {
      opt: {

      },
      tp: null
    }
  },
  mounted () {
    this.init()
  },
  components: {

  },
  computed: {
    // tpanorama ()
  },
  methods: {
    init () {
      this.opt = {
        container: 'panoramaConianer',//容器
        url: 'img/p3.png',
        lables: [
          { position: { lon: -72.00, lat: 9.00 }, logoUrl: 'img/icon.png', text: '蓝窗户' },
          { position: { lon: 114.12, lat: 69.48 }, logoUrl: 'img/icon.png', text: '一片云彩' },
          { position: { lon: 132.48, lat: -12.24 }, logoUrl: 'img/icon.png', text: '大海' },
          { position: { lon: 23.40, lat: 6.84 }, logoUrl: 'img/icon.png', text: '栅栏' }
        ],
        widthSegments: 60,//水平切段数
        heightSegments: 40,//垂直切段数（值小粗糙速度快，值大精细速度慢）
        pRadius: 1000,//全景球的半径，推荐使用默认值
        minFocalLength: 6,//镜头最a小拉近距离
        maxFocalLength: 100,//镜头最大拉近距离
        sprite: 'icon',
        onClick: this.onClick,
      }
      console.log(this.opt.onClick)
      this.tp = new tpanorama(this.opt);
    },
    onClick (name) {
      console.log(name);
      if (name.object.name === '大海') {
        this.opt.url = 'img/p2.png';
        this.opt.lables = [];
        this.tp.render(this.opt);
      }
    }


  }

}
</script>
